@import '../../../styles/settings.scss';

.productInCart {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 5px;
    padding: 10px;
    img {
        width: 90px;
        height: 90px;
        margin-right: 10px;
        border-radius: 15%;
    }
    .productDetails {
        display: flex;
        flex-direction: column;
        .productTopDetails {
            display: flex;
            justify-content: flex-start;
            width: 100%;
            .productName {
                font-size: $base-size * 2.0;
                font-weight: 600;
                margin-right: 10px;
                .productPrice {
                    font-size: $base-size * 1.5;
                    font-weight: 300;
                    margin-left: 30px;
                }
            }
        }
        .productShortDescription {
            margin-top: 5px;
            .productShortDescriptionText {
                font-size: $base-size;
            }
        }
        .productOption{
            margin-top: 3px;
            .productOptionText{
                font-size: $base-size;
                .productOptionTextTwo{
                    font-size: $base-size * 1.5;
                }
            }
        }
    }
}

.loadingSpinner {
    width: 90px;
    height: 90px;
    border: 5px solid rgba(0, 0, 0, 0.1);
    border-top: 5px solid #000;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}